import React from 'react'
import ReactModal from 'react-modal'

import InsightEditor from './InsightEditor'
import SingleInsight from './SingleInsight'

function InsightEditorModal({editing, verse, insight, setInsight, toggle}) {

  return (
    <ReactModal isOpen={editing}>
      <div className="row">
        <div className="col-6">
          <h4>Edit Insight</h4>
          <hr/>
          <InsightEditor
            content={verse.getContent()}
            verse={verse}
            insight={insight}
            setInsight={setInsight}
            toggle={toggle} />
        </div>
        <div className="col-6">
          <h4>Preview</h4>
          <hr/>
          <SingleInsight insight={insight} />
        </div>
      </div>
    </ReactModal>
  )
}

export default InsightEditorModal
