import React from 'react'

import InsightEditorModal from '../InsightEditorModal'
import InsightList from '../InsightList'
import LoadingBar from '../LoadingBar'
import Scriptures from '../../firebase/Scriptures'
import Insights from '../../firebase/Insights'
import SingleVerse from '../SingleVerse'
import useFirebaseUser from '../../firebase/FirebaseUser'
import VerseHighlightContext from '../VerseHighlightContext'

const DEFAULT_INSIGHT = {
  source: {
    label: '',
    link: ''
  },
  deltaOps: [],
  fragment: [0, 0]
}

function InsightListPage({ match: { params: { volume, book, chapter, verse: verseNumber } } }) {
  const user = useFirebaseUser()

  const [verse, setVerse] = React.useState()
  const [insights, setInsights] = React.useState()
  const [newInsight, setNewInsight] = React.useState(DEFAULT_INSIGHT)
  const [editing, setEditing] = React.useState(false)
  const [highlight, setHighlight] = React.useState({ first: 0, last: 0 })

  const contextState = {
    highlight,
    updateHighlight: (f, l) => setHighlight({ first: f, last: l })
  }

  function toggleEditing() {
    setEditing(!editing)
  }

  const scriptures = new Scriptures()
  const insightsStore = new Insights()

  React.useEffect(() => {
    scriptures.getVerse(volume, book, chapter, verseNumber, setVerse)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  React.useEffect(() => {
    insightsStore.getInsights([volume, book, chapter, verseNumber], setInsights)
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [editing])

  function addNewInsight() {
    setNewInsight(DEFAULT_INSIGHT)
    toggleEditing()
  }

  return (
    <VerseHighlightContext.Provider value={contextState}>
      <div className="row">
        <div className="col-lg-6 col-md-6 col-sm-12">
          <h4 className="text-capitalize">{book.replace(/_/g, ' ')} {chapter}:{verseNumber}</h4>
          <hr />
          <div className={editing ? null : "sticky-top"}>
            {verse ? <SingleVerse verse={verse} /> : <LoadingBar />}
            <hr />
            {verse && user.status === 'LOGGED_IN' && <button type="button" className="btn btn-warning float-right" onClick={addNewInsight}>Add Insight</button>}

            {verse ? <InsightEditorModal
              editing={editing}
              verse={verse}
              insight={newInsight}
              setInsight={setNewInsight}
              toggle={toggleEditing} /> : null }
          </div>
        </div>
        <div className="col-lg-6 col-md-6 col-sm-12">
          {verse ? null : <LoadingBar />}
          {verse ? <InsightList
            verse={verse}
            insights={insights}
            toggle={toggleEditing}
            setInsight={setNewInsight} /> : null}
        </div>
      </div>
    </VerseHighlightContext.Provider>
  )
}

export default InsightListPage
